// src/components/Flashcard.js
import React from 'react';
import './Flashcard.css'; // Import the CSS file


function Flashcard({ proverb, translation, showTranslation, toggleTranslation }) {
  return (
    <div className="flashcard" onClick={toggleTranslation}>
      <div className={`card ${showTranslation ? 'show-translation' : ''}`}>
        <div className="proverb">{proverb}</div>
        {showTranslation && <div className="translation">{translation}</div>}
      </div>
    </div>
  );
}

export default Flashcard;
