import React from 'react';
import { AppBar, Toolbar, Typography, IconButton, Menu, MenuItem } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import { styled } from '@mui/system';

const StyledAppBar = styled(AppBar)({
  backgroundColor: '#333',
  boxShadow: 'none',
  margin: 0,
});

const StyledToolbar = styled(Toolbar)({
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'right',
  maxWidth: '100%',
  margin: '0 auto',
});

const StyledTitle = styled(Typography)({
  fontWeight: 'bold',
  color: '#fff',
});

const Header = () => {
  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <StyledAppBar position="static">
      <StyledToolbar>
        <StyledTitle variant="h6" component="div">
          Proverb Flashcards
        </StyledTitle>
        <IconButton
          edge="end"
          color="inherit"
          aria-label="menu"
          aria-controls="menu"
          aria-haspopup="true"
          onClick={handleClick}
        >
          <MenuIcon />
        </IconButton>
        <Menu
          id="menu"
          anchorEl={anchorEl}A
          keepMounted
          open={Boolean(anchorEl)}
          onClose={handleClose}
        >
          <MenuItem onClick={handleClose}>Game 1</MenuItem>
          <MenuItem onClick={handleClose}>Game 2</MenuItem>
          {/* Add more menu items for additional games */}
        </Menu>
      </StyledToolbar>
    </StyledAppBar>
  );
}

export default Header;
