// src/components/FlashcardGame.js
import React, { useState, useEffect } from 'react';
import { FacebookShareButton, TwitterShareButton } from 'react-share'; // Import social media sharing components
import { Button, Grid } from '@mui/material';
import { Facebook, Twitter } from '@mui/icons-material'; // Import Material Icons
import Flashcard from './Flashcard';
import axios from 'axios';
import './FlashcardGame.css';
import Header from './Header';

function FlashcardGame() {
  const [flashcards, setFlashcards] = useState([]);
  const [currentCardIndex, setCurrentCardIndex] = useState(0);
  const [showTranslation, setShowTranslation] = useState(false);
  const [score, setScore] = useState(0);
  const [progress, setProgress] = useState(0);
  const shareUrl = 'https://daydaro.github.io/Latin-FlashCard/'; // Replace with your GitHub Pages URL
  const shareTitle = 'Check out my progress on Proverb Flashcards!';

  useEffect(() => {
    axios.get('https://raw.githubusercontent.com/Daydaro/Latin-proverbs/master/proverbs.json')
      .then(response => {
        setFlashcards(response.data);

        const storedLastViewedCardIndex = parseInt(localStorage.getItem('lastViewedCardIndex')) || 0;
        setCurrentCardIndex(storedLastViewedCardIndex);
      })
      .catch(error => {
        console.error('Error fetching flashcards:', error);
      });

    const storedScore = parseInt(localStorage.getItem('score')) || 0;
    const storedProgress = parseInt(localStorage.getItem('progress')) || 0;
    setScore(storedScore);
    setProgress(storedProgress);
  }, []);

  useEffect(() => {
    localStorage.setItem('score', score.toString());
    localStorage.setItem('progress', progress.toString());
    localStorage.setItem('lastViewedCardIndex', currentCardIndex.toString());
  }, [score, progress, currentCardIndex]);

  const toggleTranslation = () => {
    setShowTranslation(!showTranslation);
  };

  const nextCard = () => {
    setShowTranslation(false);
    const nextIndex = (currentCardIndex + 1) % flashcards.length;
    setCurrentCardIndex(nextIndex);
    setProgress(progress + 1);
  };

  const prevCard = () => {
    setShowTranslation(false);
    const prevIndex = (currentCardIndex - 1 + flashcards.length) % flashcards.length;
    setCurrentCardIndex(prevIndex);
    setProgress(progress - 1);
  };

  return (
    <div className="flashcard-game">
      <Header />
      <div className="score-progress">
        <p>Progress: {progress}/{flashcards.length}</p>
      </div>
      <div className="progress-bar-container">
        <div className="progress-bar">
          <div
            className="progress-fill"
            style={{ width: `${(progress / flashcards.length) * 100}%` }}
          ></div>
        </div>
      </div>
      <Flashcard
        proverb={flashcards[currentCardIndex]?.proverb}
        translation={flashcards[currentCardIndex]?.Translation}
        showTranslation={showTranslation}
        toggleTranslation={toggleTranslation}
      />
      <div className="separator-line"></div>
      <div className="button-container">
        <button className="back-button" onClick={prevCard}>Back</button>
        <button className="next-button" onClick={nextCard}>Next</button>
      </div>
      <div className="footer">
        <div className="share-footer-content">
          <div className="share-buttons">
            <Grid container spacing={2}>
              <Grid item>
                <FacebookShareButton url={shareUrl} quote={shareTitle}>
                  <Button variant="outlined" color="primary" startIcon={<Facebook />}>
                    Share on Facebook
                  </Button>
                </FacebookShareButton>
              </Grid>
              <Grid item>
                <TwitterShareButton url={shareUrl} title={shareTitle}>
                  <Button variant="outlined" color="primary" startIcon={<Twitter />}>
                    Share on Twitter
                  </Button>
                </TwitterShareButton>
              </Grid>
            </Grid>
          </div>
          <div className="attribution">
            Created by Daniel | Proverb Proverb Flashcards | 2023
          </div>
        </div>
      </div>
    </div>
  );
}

export default FlashcardGame;
